<template>
	<section class="page-wrap">
		<app-header/>
		<header class="section-masthead">
			<figure>
				<lazy-image :src="imgSrc" :alt="imgAlt" />
			</figure>
			<h1>Bayer</h1>
		</header>
		<article>
			<p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet architecto cupiditate deleniti dignissimos doloribus exercitationem fugiat, in inventore ipsam molestiae natus, nihil nostrum perspiciatis quae quis rem sapiente unde vitae?</p>
			<div class="video-wrapper">
				<iframe src="https://player.vimeo.com/video/81020357?title=0&byline=0"
				        title="Philadelphia Lactose Free — Launch" webkitallowfullscreen="" mozallowfullscreen=""
				        allowfullscreen="" data-ready="true" frameborder="0"></iframe>
			</div>
		</article>
		<app-footer />
	</section>
</template>

<script>
	
	import LazyImage from './LazyImage';
	import AppHeader from './AppHeader';
	import AppFooter from './AppFooter';
	
	export default {
		name: 'Philadelphia',
		components: {
			LazyImage,
			AppHeader,
			AppFooter,
		},
		data() {
			return {
				imgSrc: '/static/philadelphia.jpg',
				imgAlt: 'Philadelphia'
			}
		}
	}
</script>

<style scoped>
	.section-masthead {
		height: 100vh;
		display: flex;
		align-items: center;
		margin: 2rem -3vw;
		flex-wrap: nowrap;
	}
	
	figure {
		height: 100%;
	}
	
	img {
		height: 100%;
		display: block;
		width: 100%;
		object-fit: cover;
	}
	
	h1 {
		padding: 1em 2em;
		background-color: rgba(255, 255, 255, 0.3);
		transform: translateX(-10%);
		position: relative;
		z-index: 2;
	}
	
	article {
		margin-bottom: 2em;
	}
</style>



